import '@telerik/kendo-theme-default/styles/packages/buttons';
import { ButtonDirective } from './button.directive';
import {
    Component,
    HostBinding,
    Input,
    ContentChildren,
    QueryList,
    OnInit,
    OnDestroy,
    AfterContentChecked,
    AfterViewChecked,
    isDevMode
} from '@angular/core';
import { ButtonGroupSelection } from './selection-settings';
import { KendoButtonService } from './button.service';
import { Subscription } from 'rxjs/Subscription';
/**
 * Represents the Kendo UI ButtonGroup component for Angular.
 */
@Component({
    providers: [KendoButtonService],
    selector: 'kendo-button-group',
    template: `
        <div class="k-button-group"><ng-content select="[kendoButton]"></ng-content></div>
    `
})
export class ButtonGroupComponent implements OnInit, OnDestroy, AfterContentChecked, AfterViewChecked {
    /**
     * By default, the ButtonGroup is enabled.
     * To disable the underlying Buttons, use their own `disabled` attribute.
     *
     * For the API of the Button, refer to its
     * [configuration options article]({% slug api_buttons_buttondirective_kendouiforangular %}).
     */
    @Input('disabled') public disabled: boolean;

    /**
     * By default, the ButtonGroup selection mode is set to `multiple`.
     */
    @Input('selection') public selection: ButtonGroupSelection = 'multiple';

    @HostBinding('attr.aria-disabled') get aria(): any {
        return this.disabled ? true : undefined;
    }

    @ContentChildren(ButtonDirective) private buttons: QueryList<ButtonDirective>;

    private subscription: Subscription;

    constructor(private service: KendoButtonService) {}

    public ngOnInit(): void {
        this.subscription = this.service.buttonClicked$.subscribe((button) => {
            if (this.selection === 'single') {
                this.deactivate(this.buttons.filter(current => current !== button));
                button.selected = true;
            } else {
                button.selected = !button.selected;
            }
        });
    }

    public ngAfterViewChecked(): void {
        if (this.buttons.length) {
            this.buttons.first.renderer.setElementClass(this.buttons.first.element, 'k-group-start', true);
            this.buttons.last.renderer.setElementClass(this.buttons.last.element, 'k-group-end', true);
        }
    }

    public ngOnDestroy(): void {
        this.subscription.unsubscribe();
    }

    public ngAfterContentChecked(): void {
        this.verifySettings();
    }

    protected deactivate(buttons: Array<ButtonDirective>): void {
        buttons.forEach((button) => {
            button.selected = false;
        });
    }

    private verifySettings(): void {
        if (isDevMode()) {
            if (this.selection === 'single' && this.buttons.filter(button => button.selected).length > 1) {
                throw new Error('Having multiple selected buttons with single selection mode is not supported');
            }
        }
    }

}



